<template>
 <div id="myList" class="myList">
                     <el-dialog title="表单弹框" :visible.sync="dialogVisible" width="30%">
                  <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="商品名" >
                      <el-input v-model="form.goodsName" ></el-input>
                    </el-form-item>
                    <el-form-item label="简介">
                      <el-input v-model="form.goodsIntro"></el-input>
                    </el-form-item>
                    <el-form-item label="详情">
                      <el-input v-model="form.goodsDetailContent"></el-input>
                    </el-form-item>
                    <el-form-item label="售价">
                      <el-input v-model="form.sellingPrice"></el-input>
                    </el-form-item>
                  </el-form>
                  <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="undateItem()">确 定</el-button>
                  </span>
                </el-dialog>
    <ul>
      <li v-for="item in product" :key="item.goodsId">
 
        
          <img :src="item.goodsCoverImg" alt />
          <h2>{{item.goodsName}}</h2>
          <h3>{{item.goodsIntro}}</h3>
          <p>
            <span>{{item.sellingPrice}}金币</span>
            <span
              v-show="item.originalPrice != item.sellingPrice"
              class="del"
            >{{item.originalPrice}}金币</span>
          </p>
       
          <div class="buttonthis">

              <el-button type="primary" size="mini" @click="delItem(item.goodsId)">删除</el-button>
              <el-button type="success" size="mini"   @click="undateItemID(item)">修改</el-button>
          </div>
 
      </li>
      
    </ul>
  </div>
</template>

<script>
export default {
  data(){
    
    return{
      changeID:"",
        dialogVisible:false,
        form: {
          goodsName:'',
          goodsIntro:'',
          goodsDetailContent:"",
          sellingPrice:"",
        },
      product:"",
    }

  },
    created(){
      this.getPromo()

    },
     watch: {
      changeID: function(val){

          console.log(val);
          if(val != ""){
            this.form.goodsName=val.goodsName
            this.form.goodsIntro=val.goodsIntro
            this.form.goodsDetailContent=val.goodsDetailContent
            this.form.sellingPrice=val.sellingPrice
          }
      }
    },
    methods:{
        undateItemID(val){
        this.changeID=val 
        console.log("this.changeID=val",this.changeID);
        this.dialogVisible = true
        },
      undateItem(){
          this.dialogVisible = false
       this.$axios(   
            {
            //  url: "http://localhost:3000/cart", 
           url: this.$target+ "/admin/storesInfo/goods/update",
              method: 'post',
              params:{
                    goodsId:this.changeID.goodsId,
                    goodsName: this.form.goodsName,
                    goodsIntro:this.form.goodsIntro,
                    goodsDetailContent:this.form.goodsDetailContent,
                    sellingPrice:this.form.sellingPrice
 
              }
        })
        .then(res => {
          if(res.data.flag){
                this.notifySucceed(res.data.msg);
            }
            else{
              this.notifyError("更新 失败",res.data.msg);
            }
          })
        .catch(err => {
          return Promise.reject(err);
        });  this.changeID="";this.$router.go(0)
      },
       delItem(val){
            this.$axios(   
              {
                  // url: "http://localhost:3000/cart", 
                url: this.$target+"/admin/storesInfo/goods/delete", 
                method: 'post',
                params:{
                  goodsId: val,
                }
          })
          .then(res => {
             if(res.data.flag){
                 this.notifySucceed(res.data.msg);
             }
             else{
                this.notifyError(res.data.msg);
             }
          })
          .catch(err => {
            return Promise.reject(err);
          });     
       },
       getPromo( ) {
       this.$axios(   
            {
                // url: "http://localhost:3000/cart", 
              url: this.$target+"/goods", 
              method: 'get',
              params:{
              }
        })
        .then(res => {
           this.product = res.data.data;
         })
        .catch(err => {
          return Promise.reject(err);
        });     
    }
  }
}
</script>

<style scoped>
.myList ul li {
  z-index: 1;
  float: left;
  width: 234px;
  height: 300px;
  padding: 10px 0;
  margin: 0 0 14.5px 13.7px;
  background-color: white;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  position: relative;
}
.myList ul li:hover {
  z-index: 2;
  -webkit-box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  -webkit-transform: translate3d(0, -2px, 0);
  transform: translate3d(0, -2px, 0);
}
.myList ul li img {
  display: block;
  width: 200px;
  height: 160px;
  background: url(../assets/imgs/placeholder.png) no-repeat 50%;
  margin: 0 auto;
}
.myList ul li h2 {
  margin: 25px 10px 0;
  font-size: 14px;
  font-weight: 400;
  color: #333;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.buttonthis{
  margin: 5px 10px;
  height: 50px;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.myList ul li h3 {
  margin: 5px 10px;
  height: 18px;
  font-size: 12px;
  font-weight: 400;
  color: #b0b0b0;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.myList ul li p {
  margin: 10px 10px 10px;
  text-align: center;
  color: #ff6700;
}
.myList ul li p .del {
  margin-left: 0.5em;
  color: #b0b0b0;
  text-decoration: line-through;
}
.myList #more {
  text-align: center;
  line-height: 280px;
}
.myList #more a {
  font-size: 18px;
  color: #333;
}
.myList #more a:hover {
  color: #ff6700;
}
.myList ul li .delete {
  position: absolute;
  top: 10px;
  right: 10px;
  display: none;
}
.myList ul li:hover .delete {
  display: block
}
.myList ul li .delete:hover {
  color: #ff6700;
}
</style>
